header,
footer {
    background-color: #008ad0;
    color: white;
}

header > .container {
    padding: 20px;
}

.title {
    font-size: large;
    font-weight: bold;
}

main > .container {
  max-width: 1024px;
  padding: 50px;
  margin: 0 auto;
}

@media screen and (max-width: 576px) {
  main > .container {
    padding: 50px 20px;
  }
}

.usage,
.generator,
.links {
  margin-top: 50px;
}

h3 {
  font-size: large;
  font-weight: bold;
  margin-bottom: 20px;
}

p {
  margin-top: 10px;
}

p::before {
  content: "　";
}

.radio-label {
  display: inline-block;
  margin: 5px 0 5px 10px;
  cursor: pointer;
}

input[type=radio] {
  display: none;
}

label {
  background-color: rgba(93, 193, 240, 0.1);
  display: block;
  padding: 5px 10px;
  border-radius: 5px;
}

input[type=radio]:checked + label {
  background-color: rgba(93, 193, 240, 0.5);
}

.buttons {
  text-align: center;
  margin: 50px 0;
}

.button {
  background-color: #5dc1f0;
  color: white;

  display: inline-block;

  padding: 10px 20px;
  border-radius: 10px;
  margin: 10px;

  opacity: 0.5;
}

.button--active {
  cursor: pointer;
  opacity: 1;
}

.button--active:active {
  background-color: #008ad0;
}

.word-list {
  margin: 20px 0;
}

.word-item {
  background-color: rgba(93, 193, 240, 0.1);

  display: inline-block;

  padding: 5px 10px;
  border-radius: 5px;
  margin: 5px 10px 5px 0;
}

.word-item--special {
  background-color: rgba(248, 178, 0, 0.1);
}

.chart {
  max-width: 425px;
  margin: 0 auto;
  margin-top: 50px;
}

.link-item {
  margin-top: 10px;
}

.link-item::before {
  content: "•";
  color: #f8b200;
  display: inline-block;
  margin-right: 10px;
}

.link-item > a {
  color: #008ad0;
  cursor: pointer;
}

footer > .container {
  text-align: center;
  padding: 50px 0;
}

.copyright {
  font-size: small;
}

